const SharedForm = ({
  initialValues,
  validationSchema,
  handleSubmit,
  router,
  nameForm,
  urlRouter,
  isRegisterForm,
  nameBut,
}) => {
  return (
    <Wrapper>
      <Formik
        initialValues={initialValues}
        onSubmit={handleSubmit}
        validationSchema={validationSchema}
      >
        {({ isSubmitting }) => (
          <StyledForm>
            <WordForm>{nameForm}</WordForm>
            <List>
              <li>
                {isRegisterForm && (
                  <div>
                    <StyledField
                      type="text"
                      id="name"
                      name="name"
                      placeholder="Name"
                    />
                    <ErrorMessage name="name" component="div" />
                  </div>
                )}
              </li>
              <li>
                <div>
                  <StyledField
                    type="email"
                    id="email"
                    name="email"
                    placeholder="Email"
                  />
                  <ErrorMessage name="email" component="div" />
                </div>
              </li>
              <li>
                <div>
                  <StyledField
                    type="password"
                    id="password"
                    name="password"
                    placeholder="Password"
                  />
                  <ErrorMessage name="password" component="div" />
                </div>
              </li>
            </List>

            <SubButton type="submit">{nameBut}</SubButton>
          </StyledForm>
        )}
      </Formik>

      <Link to={`/${urlRouter}`}>
        <Router>{router}</Router>
      </Link>
    </Wrapper>
  );
};

export default SharedForm;
